.feature-container {
  display: -ms-flexbox;
  display: flex;
  font-family: "CiscoSansTTLight", sans-serif;
}

.feature-headline {
  color: #1b1c1d;
}

.Feature-sub-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Feature-Accodion-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  cursor: pointer;
}

.Feature-Accordion-indicator {
  max-height: calc((100%) - 8px);
  margin: 4px 0 4px 0;
  background-color: #00bceb;
  border-radius: 4px;
}

.Feature-Accordion-indicator-auto {
  /* max-height: calc((100%) - 8px); */
  height: 20%;
  opacity: 0;
  margin-left: -1px !important;
  border: 0;
  padding: 0;
  background-color: #00bceb;
  border-radius: 4px;
  min-width: 4px;
  max-width: 4px;
  -webkit-transition: height 3s ease-out;
  -o-transition: height 3s ease-out;
  transition: height 3s ease-out;
}

.Feature-Accorddion-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.Feature-Accordion-title {
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.Feature-Accordion-content {
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.Feature-Accordion-content .secondaryCTA-launch {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: relative;
}

.secondaryCTA-launch::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 118px;
  height: 1px;
  background-color: #0076d5;
  -webkit-transform: scale(0, 1);
      -ms-transform: scale(0, 1);
          transform: scale(0, 1);
}

.secondaryCTA-launch:hover::after {
  -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
          transform: scale(1, 1);
}

.secondaryCTA-button {
  display: -ms-flexbox;
  display: flex;
  padding-right: 11.5px;
  font-style: normal;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #0076d5;
  margin-right: 0px;
  padding-top: 0.5px;
  padding-bottom: 2.5px;
  -webkit-transition: margin-right padding-top 0.1s;
  -o-transition: margin-right padding-top 0.1s;
  transition: margin-right padding-top 0.1s;
  font-family: "CiscoSansTT-Medium", sans-serif;
}

.secondaryCTA-button:hover {
  margin-right: 2px;
  padding-top: 0px;
}

.secondaryCTA-icon-greater {
  display: -ms-flexbox;
  display: flex;
  margin-top: 4.5px;
  margin-left: 0px;
  padding-top: 3px;
  width: 5.5px;
  height: 10.98px;
  -webkit-transition: margin-left 0.1s;
  -o-transition: margin-left 0.1s;
  transition: margin-left 0.1s;
  fill: #0076d5;
}

.secondaryCTA-icon-greater:hover {
  margin-left: 2px;
}

.is-visible {
  opacity: 1;
  visibility: visible;
  /* transform: translate(0, 50%); */
  -webkit-transition: opacity 1000ms ease-out;
  -o-transition: opacity 1000ms ease-out;
  transition: opacity 1000ms ease-out;
  will-change: opacity, visibility;
}

.not-visible {
  opacity: 0;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  visibility: hidden;
}

