/* For mobile phones: */
@media (min-width: 320px) {
  .feature-container {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .feature-headline {
    font-size: 32px;
    line-height: 40px;
    margin: 48px 5vw 0 5vw;
    padding-left: 16px;
  }

  .Feature-Accodion-container {
    margin: 21px 5vw 0 5vw;
  }

  .Feature-Accorddion-card {
    margin: 0 8px 0 16px;
  }

  .Feature-Accordion-title {
    font-size: 20px;
    line-height: 28px;
  }

  .Feature-Accordion-content {
    font-size: 14px;
    line-height: 20px;
    margin: 12px 0 0 0;
  }

  .secondaryCTA-launch {
    margin: 36px 0 0 5vw;
    padding-left: 20px;
  }

  .carousel-image {
    margin: 24px 0 0 5vw;
    width: 95vw;
    height: calc((95vw) / 89.58vw);
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .feature-container {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .feature-headline {
    font-size: 32px;
    line-height: 40px;
    margin: 64px 10.42vw 0 10.42vw;
    padding-left: 24px;
  }

  .Feature-Accodion-container {
    margin: 24px 10.42vw 0 10.42vw;
  }

  .Feature-Accorddion-card {
    margin: 0 0 0 24px;
  }

  .Feature-Accordion-title {
    font-size: 20px;
    line-height: 28px;
  }

  .Feature-Accordion-content {
    font-size: 16px;
    line-height: 24px;
    margin: 12px 65px 0 0;
  }

  .secondaryCTA-launch {
    margin: 36px 0 0 10.42vw;
    padding-left: 28px;
  }

  .carousel-image {
    margin: 39px 0 19px 10.42vw;
    width: 89.58vw;
    height: calc((89.58vw) / 1.428);
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .feature-container {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .feature-container-left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 50%;
    margin: 80px 0 0 8.89%;
    padding: 0 0 0 24px;
  }

  .feature-headline {
    font-size: 48px;
    line-height: 56px;
    margin: 24px 97px 28px 0;
    padding: 0;
  }

  .Feature-Accodion-container {
    margin: 20px 58px 0 0;
  }

  .Feature-Accorddion-card {
    margin: 0 0 0 20px;
  }

  .Feature-Accordion-title {
    font-size: 24px;
    line-height: 32px;
  }

  .Feature-Accordion-content {
    font-size: 16px;
    line-height: 24px;
    margin: 12px 65px 0 0;
  }

  .secondaryCTA-launch {
    margin: 56px 0 0 28px;
    padding-left: 0;
  }

  .feature-container-right {
    width: 50%;
    margin: 80px 0 80px 0;
    padding: 0 0 0 24px;
  }

  .carousel-image {
    width: calc((50vw) - 24px);
    height: calc(((50vw) - 24px) / 1.428);
    margin: 0;
  }
}



