.nav-secondary-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-size: 14px;
  line-height: 20px;
  gap: 64px;

  position: sticky;
  top: 0;
  background-color: #ffffff;
  z-index: 9999;

  cursor: pointer;
}

.nav-desktop-overview {
  font-family: "CiscoSansTT-Medium", sans-serif;
  color: #1b1c1d;
}

.nav-desktop-item {
  font-family: "CiscoSansTTLight", sans-serif;
  color: #606162;
}

.nav-desktop-item-active {
  font-family: "CiscoSansTT-Medium", sans-serif;
  color: #0076d5;
}

.nav-secondary-accordion-upper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.nav-secondary-accordion-upper-left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  gap: 8px;
}

.nav-secondary-button {
  background-color: #0076d5;
  border: 1px solid #0076d5;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #ffffff;
  font-family: "CiscoSansTT-Medium", sans-serif;
  -webkit-transition: color background-color border-color 0.1s;
  -o-transition: color background-color border-color 0.1s;
  transition: color background-color border-color 0.1s;
}

.nav-secondary-button:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

.nav-secondary-container-left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  gap: 64px;
}

.nav-secondary-container-right {
  display: -ms-flexbox;
  display: flex;
  justify-self: flex-end;
}

.nav-secondary-accordion-menu {
  -webkit-box-shadow: 0px 8px 8px rgba(27, 28, 29, 0.1);
          box-shadow: 0px 8px 8px rgba(27, 28, 29, 0.1);
}

