/* For mobile phones: */
@media (min-width: 320px) {
  .nav-secondary-container {
    padding: 0;
  }

  .nav-secondary-accordion-upper {
    width: 90vw;
    padding: 1px 5vw 1px 5vw;
    border-bottom: 1px solid #e6e7e8;
  }

  .nav-secondary-accordion-menu {
    padding: 0 5vw 11px 5vw;
  }

  .nav-desktop-overview {
    padding: 13px 0 10px 0;
  }

  .nav-desktop-item {
    padding: 13px 0 10px 0;
  }

  .nav-secondary-accordion-menu-mobile-button {
    margin: 14px 0 32px 0;
  }

  .nav-secondary-button {
    width: 146px;
    height: 32px;
    border-radius: 24px;
    padding: 8px, 16px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .nav-secondary-container {
    padding: 0;
  }

  .nav-secondary-accordion-upper {
    width: 79.18vw;
    padding: 6px 10.42vw 9px 10.42vw;
    border-bottom: 1px solid #e6e7e8;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .nav-secondary-accordion-upper-right {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
  }

  .nav-secondary-accordion-menu {
    padding: 0px 10.42vw;
  }

  .nav-desktop-overview {
    padding: 13px 0 10px 0;
  }

  .nav-desktop-item {
    padding: 13px 0 10px 0;
  }

  .nav-secondary-button {
    width: 146px;
    height: 32px;
    border-radius: 24px;
    padding: 8px, 16px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
  }
}

@media all and (min-width: 1024px) {
  /* For desktop */
  .nav-secondary-container {
    padding: 18px 6.25vw 17px 6.25vw;
    padding-top: 18px;
    padding-bottom: 17px;
    padding-left: 6.25vw;
    padding-right: 6.25vw;
    border-bottom: 1px solid #e6e7e8;
  }

  .nav-desktop-item:hover {
    color: #0076d5;
