  }

  .nav-desktop-item-active {
    padding: 0;
  }

  .nav-secondary-container-right {
    margin: 5px 0;
  }

  .nav-secondary-button {
    width: 162px;
    height: 40px;
    border-radius: 48px;
    padding: 12px, 24px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
  }
}

@media all and (min-width: 1440px) {
