  .nav-secondary-container {
    padding-top: 23px;
    padding-bottom: 22px;
    padding-left: 8.89vw;
    padding-right: 8.89vw;
    border-bottom: 1px solid #e6e7e8;
  }

  .nav-desktop-item:hover {
    color: #0076d5;
  }

  .nav-desktop-overview {
    padding: 0;
  }

  .nav-desktop-item {
    padding: 0;
  }

  .nav-desktop-item-active {
    padding: 0;
  }

  .nav-secondary-container-right {
    margin: 0;
  }

  .nav-secondary-button {
    width: 162px;
    height: 40px;
    border-radius: 48px;
    padding: 12px, 24px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
  }
}



